<template>
  <div class="plan-master-layout" :class="{ 'dark-mode': isDarkMode }">
    <aside class="sidebar">
      <div class="sidebar-header">
        <h2>AI</h2>
        <p class="subtitle"></p>
      </div>
      <div class="chat-list">
        <div
          v-for="chat in chatList"
          :key="chat.chatId"
          :class="['chat-item', chat.chatId === currentChatId ? 'active' : '']"
          @click="switchChat(chat.chatId)"
        >
          <div class="chat-summary">
            {{ chat.summary || '' }}
          </div>
          <div class="chat-time">{{ formatTime(chat.createTime) }}</div>
        </div>
      </div>
      <button class="new-chat-btn" @click="newChat">+ </button>
    </aside>
    
    <main class="chat-main">
      <div class="chat-header" :class="{ 'collapsed': isHeaderCollapsed }">
        <div class="header-content">
          <h1>AI</h1>
          <div class="features">
            <span class="feature-tag"></span>
            <span class="feature-tag"></span>
            <span class="feature-tag"></span>
            <span class="feature-tag"></span>
          </div>
        </div>
        <button class="collapse-btn" @click="toggleHeader" :title="isHeaderCollapsed ? '' : ''">
          {{ isHeaderCollapsed ? '' : '' }}
        </button>
      </div>
      
      <div class="chat-window">
        <div class="messages" ref="messagesContainer">
          <div v-for="(msg, idx) in currentMessages" :key="idx" :class="['message', msg.role]">
            <div class="message-avatar">
              <div v-if="msg.role === 'user'" class="user-avatar"></div>
              <div v-else class="ai-avatar"></div>
            </div>
            <div class="message-content">
              <!--  -->
              <template v-if="msg.role === 'user'">
                <div class="user-message">
                  <span class="text">{{ msg.text }}</span>
                </div>
              </template>
              
              <!-- AI -->
              <template v-else>
                <!--  -->
                <div v-if="loading && idx === currentMessages.length - 1 && !msg.text" class="loading-dots">
                  <span class="dot"></span>
                  <span class="dot"></span>
                  <span class="dot"></span>
                </div>
                
                <!-- AI -->
                <div v-if="msg.text" class="ai-response">
                  <div class="response-text" v-html="formatResponse(msg.text)"></div>
                </div>
              </template>
            </div>
          </div>
        </div>
        
        <div class="input-bar">
          <div class="input-wrapper">
            <textarea
              v-model="input"
              @input="autoResizeInput"
              @keydown="handleKeydown"
              placeholder="..."
              :disabled="loading"
              class="input-auto textarea-auto"
              rows="1"
              style="resize:none;overflow:hidden;"
            />
            <div class="input-actions">
              <button class="send-btn" @click="send" :disabled="loading || !input.trim()">
                <span v-if="loading">...</span>
                <span v-else></span>
              </button>
            </div>
          </div>
          
          <!--  -->
          <div class="quick-actions">
            <button class="quick-btn" @click="quickAction('')"></button>
            <button class="quick-btn" @click="quickAction('')"></button>
            <button class="quick-btn" @click="quickAction('')"></button>
            <button class="quick-btn" @click="quickAction('')"></button>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { ref, watch, onMounted, nextTick } from 'vue'
import axios from 'axios'
import { marked } from 'marked'

function genId() {
  return 'planner_' + Date.now() + '_' + Math.random().toString(36).slice(2, 8)
}

export default {
  name: 'PlanMaster',
  setup() {
    const chatList = ref([])
    const currentChatId = ref('')
    const input = ref('')
    const loading = ref(false)
    const currentMessages = ref([])
    const messagesContainer = ref(null)
    const isDarkMode = ref(false)
    const isHeaderCollapsed = ref(false)
    
    // 
    const chatMessages = ref({})
    
    // 
    onMounted(async () => {
      await fetchChatList()
      if (chatList.value.length === 0) {
        newChat()
      } else {
        switchChat(chatList.value[0].chatId)
      }
      
      // 
      initDarkMode()
      
      // 
      const observer = new MutationObserver(() => {
        updateDarkMode()
      })
      observer.observe(document.body, {
        attributes: true,
        attributeFilter: ['class']
      })
    })
    
    // 
    const fetchChatList = async () => {
      try {
        const resp = await axios.get('/planner/history/planner/with-time')
        // {chatId: createTime} 
        const chatIdsWithTime = resp.data
        const chatListWithTitles = []
        
        // 
        for (const [chatId, createTime] of Object.entries(chatIdsWithTime)) {
          try {
            const messagesResp = await axios.get(`/planner/history/planner/${chatId}`)
            const messages = messagesResp.data
            let title = ''
            let lastUpdateTime = createTime // 
            
            if (messages.length > 0) {
              // 
              const firstUserMessage = messages.find(msg => msg.role === 'user')
              if (firstUserMessage && firstUserMessage.content) {
                title = firstUserMessage.content.slice(0, 20) + (firstUserMessage.content.length > 20 ? '...' : '')
              }
              
              // 
              // 
              if (messages.length > 0) {
                // 
                // 
                lastUpdateTime = createTime
              }
            }
            
            chatListWithTitles.push({ 
              chatId, 
              summary: title,
              createTime: lastUpdateTime
            })
          } catch (error) {
            // 
            chatListWithTitles.push({ 
              chatId, 
              summary: '',
              createTime: createTime
            })
          }
        }
        
        // 
        chatListWithTitles.sort((a, b) => b.createTime - a.createTime)
        
        chatList.value = chatListWithTitles
        // 
        if (chatList.value.length > 0 && !currentChatId.value) {
          currentChatId.value = chatList.value[0].chatId
        }
      } catch (error) {
        console.error(':', error)
      }
    }
    
    // 
    const newChat = () => {
      const chatId = genId()
      currentChatId.value = chatId
      chatMessages.value[chatId] = []
      currentMessages.value = []
      // 
    }
    
    // 
    const fetchMessages = async (chatId) => {
      if (!chatId) return
      try {
        const resp = await axios.get(`/planner/history/planner/${chatId}`)
        // resp.data: [{role, content}]
        const messages = resp.data.map(msg => {
          if (msg.role === 'assistant') {
            return { role: 'ai', text: msg.content }
          } else {
            return { role: 'user', text: msg.content }
          }
        })
        
        // 
        chatMessages.value[chatId] = messages
        // currentMessages
        if (chatId === currentChatId.value) {
          currentMessages.value = messages
          scrollToBottom()
        }
      } catch (error) {
        console.error(':', error)
      }
    }

    // 
    const switchChat = async (chatId) => {
      currentChatId.value = chatId
      // 
      if (chatMessages.value[chatId]) {
        currentMessages.value = chatMessages.value[chatId]
        scrollToBottom()
      } else {
        // 
        await fetchMessages(chatId)
      }
    }
    
    // 
    const send = async () => {
      if (!input.value.trim() || loading.value) return
      
      const chatId = currentChatId.value
      //  chatId 
      if (!chatId) {
        const newId = genId()
        currentChatId.value = newId
        chatMessages.value[newId] = []
      }
      
      const prompt = input.value.trim()
      input.value = ''
      loading.value = true
      
      // 
      let messages = chatMessages.value[currentChatId.value] || []
      
      // 
      if (messages.length === 0) {
        const currentTime = Date.now()
        chatList.value.unshift({ 
          chatId: currentChatId.value, 
          summary: prompt.slice(0, 20) + (prompt.length > 20 ? '...' : ''),
          createTime: currentTime
        })
      } else {
        // 
        const currentChat = chatList.value.find(c => c.chatId === currentChatId.value)
        if (currentChat) {
          currentChat.createTime = Date.now()
        }
      }
      
      // 
      messages.push({ role: 'user', text: prompt })
      chatMessages.value[currentChatId.value] = messages
      
      // AI
      messages.push({ role: 'ai', text: '' })
      chatMessages.value[currentChatId.value] = messages
      
      // currentMessages
      if (currentChatId.value === currentChatId.value) {
        currentMessages.value = messages
        scrollToBottom()
      }
      
      // AI
      processAIResponse(currentChatId.value, prompt, messages)
    }

    // AI
    const processAIResponse = async (chatId, prompt, messages) => {
      try {
        //  fetch 
        const url = axios.getUri({ url: '/planner/chat', params: { prompt, chatId } })
        const resp = await fetch(url, { method: 'GET' })
        if (!resp.body) throw new Error('')
        const reader = resp.body.getReader()
        let aiText = ''
        
        // AI
        const tempMsgIdx = messages.length - 1
        
        // currentMessages
        if (chatId === currentChatId.value) {
          currentMessages.value = messages
          scrollToBottom()
        }
        
        // 
        let reading = true
        while (reading) {
          const { done, value } = await reader.read()
          if (done) {
            reading = false
            break
          }
          
          const chunk = new TextDecoder('utf-8').decode(value)
          aiText += chunk
          
          // 
          messages[tempMsgIdx].text = aiText
          
          // currentMessages
          if (chatId === currentChatId.value) {
            currentMessages.value = messages
            await nextTick()
            scrollToBottom()
          }
        }
        
      } catch (error) {
        console.error(':', error)
        // 
        messages[messages.length - 1].text = ''
        if (chatId === currentChatId.value) {
          currentMessages.value = messages
        }
      } finally {
        loading.value = false
        scrollToBottom()
        // 
        await fetchChatList()
      }
    }
    

    
    // 
    const handleKeydown = (e) => {
      if (e.key === 'Enter') {
        if (e.shiftKey) {
          // Shift+Enter: 
          return;
        } else {
          // Enter: 
          e.preventDefault();
          send();
        }
      }
    };

    // 
    const autoResizeInput = (event) => {
      const textarea = event.target
      textarea.style.height = 'auto'
      textarea.style.height = Math.min(textarea.scrollHeight, 120) + 'px'
    }
    
    // 
    const scrollToBottom = async () => {
      await nextTick()
      if (messagesContainer.value) {
        messagesContainer.value.scrollTop = messagesContainer.value.scrollHeight
      }
    }
    
    // 
    const formatResponse = (text) => {
      if (!text) return ''
      
      try {
        // markedmarkdown
        return marked(text, {
          breaks: true, // 
          gfm: true, // GitHubmarkdown
          sanitize: false // HTML
        })
      } catch (error) {
        console.error('Markdown:', error)
        // markdown
        return text.replace(/\n/g, '<br>')
      }
    }
    
    // 
    const formatTime = (timestamp) => {
      if (timestamp) {
        const date = new Date(timestamp)
        return date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
      }
      return ''
    }
    
    // 
    const quickAction = (action) => {
      let prompt = ''
      switch (action) {
        case '':
          prompt = ''
          break
        case '':
          prompt = ''
          break
        case '':
          prompt = ''
          break
        case '':
          prompt = ''
          break
      }
      input.value = prompt
    }
    
    // 
    const initDarkMode = () => {
      isDarkMode.value = document.body.classList.contains('dark')
    }
    
    // 
    const updateDarkMode = () => {
      isDarkMode.value = document.body.classList.contains('dark')
    }
    
    // 
    const toggleHeader = () => {
      isHeaderCollapsed.value = !isHeaderCollapsed.value
    }
    
    // 
    watch(currentMessages, () => {
      scrollToBottom()
    }, { deep: true })
    
    return {
      chatList,
      currentChatId,
      input,
      loading,
      currentMessages,
      messagesContainer,
      isDarkMode,
      isHeaderCollapsed,
      newChat,
      switchChat,
      send,
      autoResizeInput,
      formatResponse,
      handleKeydown,
      formatTime,
      quickAction,
      toggleHeader,
      fetchChatList,
      fetchMessages,
      processAIResponse
    }
  }
}
</script>

